import React, {ForwardedRef} from 'react';
import {Avatar} from '@here/hds-avatar';

// eslint-disable-next-line no-new
new Avatar();

const HDSAvatarButton = React.forwardRef(
  (props: React.HTMLAttributes<HTMLButtonElement>, ref: ForwardedRef<HTMLElement>) => {
    const {children, ...rest} = props;
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    // const clone: any = React.isValidElement(children)
    //   ? React.cloneElement(children, {ref})
    //   : children;
    const elementProps: React.HTMLAttributes<HTMLButtonElement> = {
      ...rest,
      role: 'button',
      tabIndex: 0,
    };
    return (
      <span className="hds-avatar-wrapper" {...elementProps} ref={ref}>
        {children}
      </span>
    );
  }
);

export default HDSAvatarButton;
